				"html": "<p>Recently, I upgraded to El Capitan. Suddenly my local websites using vagrant weren’t working &#x26; I had to reinstall some of my ruby gems like jekyll, what I use for this blog.</p>\n<p>Below are a short list of things that helped me get back up &#x26; running again. Hope it helps you too.</p>\n<ul>\n<li>Vagrant\n<a href=\"https://davidturner.name/setting-up-vagrant-in-os-x-10-11-el-capitan/\">Quickguide</a></li>\n</ul>\n<p>^ Follow the instructions in the link provided</p>\n<ul>\n<li>Ruby\n<a href=\"https://github.com/jekyll/jekyll/issues/3984\">Various Ruby Updates - this link is for Jekyll</a></li>\n</ul>\n<p>^ Several ruby gems (like Jekyll) may not work not work</p>\n<p>Re-installed homebrew if you have to (copy/paste the line of code below into terminal)</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>ruby <span class=\"token operator\">-</span>e <span class=\"token string\">\"$(curl -fsSL https://raw.githubusercontent.com/Homebrew/install/master/install)\"</span>\n</code></pre>\n      </div>\n<p>Re-install ruby if you have to</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>brew install ruby\n</code></pre>\n      </div>\n<p>Re-install your gems as needed</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>gem install <span class=\"token punctuation\">[</span>gem name<span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>",
